@font-face {
  font-style: normal;
  font-family: "Montserrat";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/montserratRegular.woff2") format("woff2"), url("../fonts/montserratRegular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-family: "Montserrat";
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/montserratMedium.woff2") format("woff2"), url("../fonts/montserratMedium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-family: "Montserrat";
  font-weight: 600;
  font-display: swap;
  src: url("../fonts/montserratSemiBold.woff2") format("woff2"), url("../fonts/montserratSemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-family: "Montserrat";
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/montserratBold.woff2") format("woff2"), url("../fonts/montserratBold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden, .contacts h2,
.contacts__main-header, .callback-form input[type=checkbox], .callback-form input[type=text] + label,
.callback-form input[type=tel] + label,
.callback-form input[type=email] + label {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 1366px;
  margin: 0 auto;
  padding-left: 103px;
  padding-right: 103px;
}
@media (max-width: 1365px) {
  .container {
    padding-left: calc(50vw - 580px);
    padding-right: calc(50vw - 580px);
  }
}
@media (max-width: 1279px) {
  .container {
    padding-left: 45px;
    padding-right: 45px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding-left: 45px;
    padding-right: 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}
@media (max-width: 374px) {
  .container {
    width: 320px;
  }
}

.btn {
  display: flex;
  padding: 14px 46px 15px 47px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  justify-content: center;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.main-header {
  color: #f9fbfd;
  background-color: #011c40;
  background-image: url("../img/background/background-intro-desktop.jpg");
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .main-header {
    background-image: url("../img/background/background-intro-desktop@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .main-header {
    background-image: url("../img/background/background-intro-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 144dpi), (max-width: 1023px) and (min-resolution: 1.5dppx) {
  .main-header {
    background-image: url("../img/background/background-intro-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .main-header {
    min-width: 320px;
    background-image: url("../img/background/background-intro-mobile.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 144dpi), (max-width: 767px) and (min-resolution: 1.5dppx) {
  .main-header {
    background-image: url("../img/background/background-intro-mobile@2x.jpg");
  }
}

.main-header-container {
  position: relative;
}

.main-header__menu-wrapper {
  box-sizing: border-box;
  min-height: 108px;
  padding-top: 35px;
  padding-bottom: 35px;
  display: grid;
  align-items: flex-start;
  grid-template-columns: minmax(70px, 15%) 1fr;
  gap: 20px;
}
@media (max-width: 1023px) {
  .main-header__menu-wrapper {
    min-height: 80px;
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .main-header__menu-wrapper {
    grid-template-columns: 1fr 1fr;
    min-height: 62px;
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.main-header__logo-link {
  display: flex;
  fill: #f9fbfd;
}

.main-header__logo-link svg {
  width: 70px;
  height: 38px;
}
@media (max-width: 1023px) {
  .main-header__logo-link svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .main-header__logo-link svg {
    width: 42px;
    height: 22px;
  }
}

.main-header__button {
  display: none;
  margin: auto 0;
  margin-left: auto;
  padding: 0;
  width: 24px;
  height: 12px;
  background-image: url("../img/svg/icon-burger.svg");
  background-color: transparent;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .main-header__button:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-header__button.focus-visible:focus {
  opacity: 0.8;
}
.main-header__button:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (max-width: 767px) {
  .main-header__button {
    display: block;
  }
}

.main-header__navigation {
  color: #f9fbfd;
  justify-self: end;
}
@media (max-width: 767px) {
  .main-header__navigation {
    grid-column: span 2;
  }
}

.main-header__intro {
  min-height: 542px;
}
@media (max-width: 1023px) {
  .main-header__intro {
    min-height: 558px;
  }
}
@media (max-width: 767px) {
  .main-header__intro {
    min-height: 416px;
  }
}

.main-navigation {
  margin-top: 1px;
