}

.main-navigation__list {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-wrap: wrap;
  gap: 33px;
  margin: auto 0;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
