}

.footer-navigation__link {
  display: flex;
  align-items: center;
  margin: 0;
  text-decoration: none;
  color: #f9fbfd;
  min-height: 38px;
  font-size: 16px;
  line-height: 20px;
}
@media (max-width: 1023px) {
  .footer-navigation__link {
    min-height: 20px;
    font-size: 12px;
    line-height: 15px;
  }
}
@media (max-width: 767px) {
  .footer-navigation__link {
    min-width: 240px;
    justify-items: flex-start;
  }
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .footer-navigation__link:hover, .footer-navigation__link:focus {
    opacity: 0.8;
  }
}

.main-footer {
  min-height: 138px;
  min-width: 320px;
  background-color: #011c40;
}

.main-footer__container {
  display: grid;
  padding-top: 50px;
  padding-bottom: 50px;
  grid-template-columns: 70px 1fr minmax(114px, max-content);
  gap: 40px;
}
@media (max-width: 1023px) {
  .main-footer__container {
    grid-template-columns: 38px 1fr minmax(114px, max-content);
    gap: 25px;
  }
}
@media (max-width: 767px) {
  .main-footer__container {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    padding-top: 40px;
    padding-bottom: 50px;
    gap: 25px;
  }
}

.main-footer__logo-link {
  display: flex;
  fill: #f9fbfd;
}

.main-footer__logo-link svg {
  width: 70px;
  height: 38px;
}
@media (max-width: 1023px) {
  .main-footer__logo-link svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .main-footer__logo-link svg {
    width: 42px;
    height: 22px;
  }
}

.main-footer__navigation {
  justify-self: flex-start;
  margin-top: 1px;
  margin-left: 1px;
}
@media (max-width: 1023px) {
  .main-footer__navigation {
    margin-top: -1px;
    margin-left: 0;
  }
}

.social {
  min-width: 114px;
}

.social__list {
  margin: 0;
  margin-top: 11px;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, 20px);
  gap: 26px;
  justify-content: end;
}
@media (max-width: 1023px) {
  .social__list {
    margin-top: 2px;
    grid-template-columns: repeat(auto-fit, 16px);
    gap: 21px;
  }
}
@media (max-width: 767px) {
  .social__list {
    margin-top: 0;
    margin-left: -4px;
    justify-content: start;
  }
}

.social__item {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 20px;
  min-width: 20px;
  background-position: center;
  background-repeat: no-repeat;
  background-color: transparent;
  background-size: contain;
}
@media (max-width: 1023px) {
  .social__link {
    min-height: 16px;
    min-width: 16px;
  }
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .social__link:hover, .social__link:focus {
    opacity: 0.8;
  }
}

.social__link--ok {
  background-image: url("../img/svg/icon-social-ok.svg");
}

.social__link--dzen {
  background-image: url("../img/svg/icon-social-dzen.svg");
}

.social__link--reddit {
  background-image: url("../img/svg/icon-social-reddit.svg");
}

